body{
    background-color: #e5e5e5;
}

.component{
    background-color: white;
    width: 90%;
    max-width: 600px;
    margin: 1em auto;
    padding-bottom: 2em;
    border: 0.1px solid black;
    border-radius: 10px;
    text-align: center;
}

.musicCover{
    width: 100%;
    max-width: 200px;
    border-radius: 10px;
    margin: 1em auto;
}

.playButton{
    background: none;
    border: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.subtitle{
    margin-top: -1rem;
    color: #4f4f4f;
}

.time {
    margin: 0 auto;
    width: 80%;
    display: flex;
    justify-content: space-between;
    color: #828282;
    font-size: smaller;
  }
  
  .timeline {
    width: 80%;
    background-color: #27ae60;
  }


/* Playlist Styles */
.playlist{
    margin: 1em auto;
    width: 90%;
    max-width: 600px;
    background-color: white;
    padding: 1rem;
    border: 0.1px solid black;
    border-radius: 10px;
}

.playlist h2 {
    text-align: center;
}

.playlist ul{
    list-style-type: none;
    padding: 0;
}

.playlist li{
    padding: 0.5rem;
    cursor: pointer;
    text-align: center;
}

.playlist li:hover{
    background-color: #e5e5e5;
    border-radius: 10px;
    -ms-zoom-animation: default;
}

/* Responsive Design */
@media (min-width: 576px) {
    .component{
        width: 80%;
    }
    .musicCover{
        max-width: 250px;
    }
}

@media (min-width: 768px) {
    .component{
        width: 70%;
    }
    .musicCover{
        max-width: 300px;
    }
}

@media (min-width: 992px) {
    .component{
        width: 60%;
    }
    .musicCover{
        max-width: 350px;
    }
}

@media (min-width: 1200px) {
    .component{
        width: 50%;
    }
    .musicCover{
        max-width: 400px;
    }
}


